import { IconButton } from '@material-ui/core';
import { MenuOpen } from '@material-ui/icons';
import React, { useEffect, useState } from 'react';
import { animateScroll as scroll } from 'react-scroll';
import { Nav, NavBarContainer, NavLogo, MobileIcon, NavMenu, NavLinks, NavItem } from './navBarStyles';

const NavBar = ({ toggleOpen }) => {

    const [scrollNav, setScrollNav] = useState(false);

    const changeNav = () => {
        if (window.scrollY >= 80) {
            setScrollNav(true);
        } else {
            setScrollNav(false);
        }
    };

    const scrollHome = () => {
        scroll.scrollToTop();
    };


    useEffect(() => {
        window.addEventListener('scroll', changeNav)
    }, [])

    return (
        <>
            <Nav scrollNav={scrollNav}>
                <NavBarContainer>
                    <NavLogo to='/' onClick={scrollHome} >
                        Home
                    </NavLogo>
                    <MobileIcon onClick={toggleOpen}>

                    </MobileIcon>
                    <NavMenu>
                        <NavItem>
                            <NavLinks
                                to="stack" smooth={true} duration={500} spy={true} offset={-75}
                            >Stack</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="cv" smooth={true} duration={500} spy={true} offset={-74} >Currículum</NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="contact" >Contacto</NavLinks>
                        </NavItem>
                    </NavMenu>
                </NavBarContainer>
            </Nav>
        </>
    )
}

export default NavBar
